    transition: all .5s ease;
    opacity: 1;
    box-sizing: content-box;
    padding-bottom: 40px;

    &.disabled {
      height: 0;
      opacity: 0;
    }
  }
  .userInput,
  .viewer {
    border: 0;
    display: block;
    line-height: 1.2em;
    width: 100%;
    color: black;
    border: 1px solid black;
    box-sizing: border-box;
    border-radius: 5px;
  }

  .userInput {
    height: 0;
    pointer-events: none;
    opacity: 0;
    border: 0;
  }

  .customText {
    padding: 10px 15px;
    margin: 50px auto 15px auto;
    display: block;
    font-size: 20px;
    width: 80vw;
    max-width: 800px;
    box-sizing: border-box;
    border-radius: 5px;
    font-size: 30px;
    transition: all .5s ease;
    height: 20vh;
    opacity: 1;

    &.disabled {
      height: 0;
      opacity: 0;
      pointer-events: none;
    }
  }
  .viewer {
    outline: 1px solid green;
    pointer-events: none;
    color: black;
    position: relative;
    z-index: 1;
    padding: 10px;
  }

  .letter {
    position: relative;
    display: inline-block;
  }

  .success {
    color: gray;
  }

  .active {
    animation: blink 600ms steps(1, start) infinite;
  }

  .letter.active.error {
    animation: blinkError 600ms steps(1, start) infinite;
  }
  .letter.active.space.error:before {
    color: red;
    opacity: 1;
  }
  .letter.active.space {
    animation: blink 600ms steps(1, start) infinite;
  }

  .space.success:before {
    color: black;
  }
  .space {
    color: gray;
    opacity: 0.3;
  }

  .articleTitle {
    max-width: 800px;
    margin: 25px auto 0 auto;
    text-align: center;
  }

  .toogleTyping {
    border: 1px solid black;
    background: #fff;
    border-radius: 3px;
    padding: 5px 10px;
    margin: 0 auto;
    display: block;
    font-size: 20px;
    outline: 0;
    cursor: pointer;
    &:hover {
      background-color: black;
      color: white;
      border-color: transparent;
    }
  }

  @keyframes blink {
    0% {
      background-color: transparent;
      color: gray;
    }
    50% {
      background-color: black;
      color: white;
    }
    100% {
      background-color: transparent;
      color: gray;
    }
  }
  @keyframes blinkError {
    0% {
      background-color: transparent;
      color: red;
    }
    50% {
      background-color: black;
      color: red;
    }
    100% {
      background-color: transparent;
      color: red;
    }
  }
</style>
